<template>
  <div>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text=""
      @load="onLoad"
    >
    <van-cell
      v-for="item in list"
      :key="item.aut_id"
      :title="item.title"
    />
    </van-list>
  </div>
</template>

<script>
import { searchResults } from '../../../api/search'
export default {
  props: {
    q: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      searchContent: '', // 
      page: 1, // 
      per_page: 10// 
    }
  },

  methods: {
    async onLoad () {
      //   
      const res = await searchResults({
        page: this.page,
        per_page: this.per_page,
        q: this.q// 
      })
      // console.log(res)
      this.list = res.data.data.results

      // 
      this.loading = false

      // 
      if (this.list.length >= 40) {
        this.finished = true
      }
    }
  }
}
</script>

